import { Button, ButtonGroup, Typography } from "@mui/material";
import { useAppDispatch, useAppSelector } from "../../store/configureStore";
import { decrement, increment } from "./counterSlice";

export default function ContactPage() {
    const dispatch = useAppDispatch();
    const { data, title } = useAppSelector(state => state.counter);
    return (
        <>
            <Typography variant='h2'>
                {title}
            </Typography>
            <Typography>
                The data is: {data}
            </Typography>
            <ButtonGroup>
                <Button onClick={() => dispatch({ type: decrement(1) })} variant='contained' color='error'>Decrement</Button>
                <Button onClick={() => dispatch({ type: increment(1) })} variant='contained' color='primary'>Increment</Button>
                <Button onClick={() => dispatch({ type: increment(5) })} variant='contained' color='secondary'>Increment by 5</Button>
            </ButtonGroup>
        </>
    )
}